/* CALENDAR LANDING */
.calendar_landing.react-calendar {
  width: 100% !important;
  border-radius: 10px;
  padding: 5px;
}

.calendar_landing .calender-body {
  background-color: #e7f3f8 !important;
}

.calendar_landing .react-calendar__tile--active {
  border:2px solid #fbbc13 !important;
  border-radius: 10px;
}

.calendar_landing .react-calendar__month-view__days__day {
  border-radius: 10px;
}

.calendar_landing .react-calendar_month-viewdays_day {
  margin: 12px !important;
}

.calendar_landing .react-calendar__tile--active:enabled:hover {
  background-color: #fbbc13 !important;
}

.calendar_landing .react-calendar button:enabled:hover {
  border-radius: 10px;
}

.calendar_landing .react-calendar__month-view__days__day--weekend {
  color: #1f1b13;
}

.calendar_landing .react-calendar__tile--now {
  background-color: #383632;
  border-radius: 10px;
  color: #696969;
}

.react-calendar__tile--now:hover {
  background-color: #919191 !important;

}

.calendar_landing .react-calendar__month-view__days__day--neighboringMonth {
  visibility: hidden;
}

.react-calendar__month-view__weekdays__weekday>abbr {
  text-decoration: none !important;
}

/* END CALENDAR LANDING */

