.MuiDateRangeCalendar-root>div:nth-child(1) {
  display: none;
}

.modal_custom_landing {
  width: 60%;
  scale:1;
  transition: scale 0.3s ease; 
  overflow-y: auto; /* Permite el scroll vertical cuando el contenido excede la altura */
  max-height: 100%;
  backface-visibility: hidden;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 2px solid #ddd !important;
  background-color: #fff;
  border-radius: 5px;
  padding: 4px;
  box-sizing: border-box; /* Asegura que el padding y el border se incluyan en el tamaño total del elemento */

  @starting-style {
    scale:0;
  }
}


.modal_custom_landing.hidden {
  scale: 0;
  transition: scale 0.3s ease;
}

