  border:1px solid #fff !important;
}

@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(10px); /* Opcional: para un efecto de subida */
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.btn_next_button{
